import React from 'react';
import { Typography, Select, MenuItem, Button } from '@material-ui/core';
import { useStyles } from './useStyles';

function GalleryHeader({ handleSort, sortOption, handleClickOpen }) {
  const classes = useStyles();

  return (
    <div className={classes.centerContent}>
      <Select value={sortOption} onChange={handleSort}>
        <MenuItem value="name"> </MenuItem>
        <MenuItem value="date">  </MenuItem>
        <MenuItem value="description"> </MenuItem>
      </Select>
      <Typography variant="h1" className={classes.h1}>
               
      </Typography>
      <Button className={classes.button} onClick={handleClickOpen}>
         
      </Button>
    </div>
  );
}

export default GalleryHeader;
